@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300&family=Roboto:wght@100;300;400;700&display=swap);
*{
  margin: 0;
}
body{font-family:'Roboto', sans-serif}.image-wrapper{position:relative}.image-wrapper .action{box-sizing:border-box;position:absolute;bottom:8px;background:rgba(0,0,0,0.58);width:calc(100% - 15px);left:50%;right:0;transform:translateX(-50%);height:0;overflow:hidden;transition:0.2s ease-in-out;padding:0 15px}.image-wrapper .action button{color:#fff}.image-wrapper .action button:hover{color:blue}.image-wrapper .action button.download-btn a{display:inline-flex;color:#fff}.image-wrapper .action button.download-btn a:hover{color:blue}.image-wrapper:hover .action{height:40px;color:#fff}.modal-single-image{padding-top:10px}.modal-single-image .MuiBox-root{height:calc(100% - 100px);overflow:auto;padding:15px}.modal-single-image .MuiBox-root::-webkit-scrollbar-track{background:#f1f1f1}.modal-single-image .MuiBox-root::-webkit-scrollbar-thumb{background:#888}.modal-single-image .MuiBox-root::-webkit-scrollbar-thumb:hover{background:#555}.modal-single-image .MuiBox-root::-webkit-scrollbar{width:10px}.flex-layout{display:flex;position:relative}.flex-layout .single-tag{position:absolute;background-color:#000;color:#fff;display:inline-block;padding:5px 15px;text-transform:uppercase;font-size:14px;font-weight:600;letter-spacing:0.5px}.flex-layout .images{width:calc(100% - 300px)}.flex-layout .related-images{position:relative;width:280px;margin-left:10px}.flex-layout .related-images .related-tag{text-transform:uppercase;position:absolute;background-color:#000;color:#fff;display:inline-block;padding:5px 15px;font-size:14px;font-weight:600;letter-spacing:0.5px}.flex-layout .related-images .inner-images{width:100%;height:140px;display:block;margin-bottom:15px;object-fit:cover}.images{width:100%;height:auto;object-fit:cover;margin-right:auto;display:block;margin-bottom:1rem}footer{background-color:#000;padding:15px;color:#fff;margin-top:80px;text-align:center;text-transform:capitalize}.pagination-wrapper{margin-top:50px;text-align:center;display:flex;justify-content:center;width:100%}

